@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@100..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 18px;
}
@media only screen and (max-width: 450px) {
  body {
    font-size: 4vw;
    line-height: 4.5vw;
  }
}

h1, h2, h3,
h4, h5, h6,
p, span, a {
  font-size: 14px;
  line-height: 16px;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: block;
}
@media only screen and (max-width: 450px) {
  h1, h2, h3,
  h4, h5, h6,
  p, span, a {
    font-size: 3.5vw;
    line-height: 4vw;
  }
}

h1, h2, h3,
h4, h5, h6 {
  font-family: "Heebo";
}

p, span, a {
  font-family: "Poppins";
}

a {
  color: #fff;
}

svg {
  width: 14px;
}

button {
  background-color: transparent;
  padding: 0;
  border: 0;
  outline: 0;
  cursor: pointer;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
}

.Header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}
@media only screen and (max-width: 768px) {
  .Header .HeaderSocial {
    display: none;
  }
}
.Header .HeaderSocial ul {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media only screen and (max-width: 768px) {
  .Header .HeaderSocial ul {
    justify-content: center;
  }
}
.Header .HeaderSocial ul li:first-child a svg {
  width: 13px;
}
.Header .HeaderSocial ul li a svg {
  width: 22px;
  fill: #fff;
  transition: all ease 0.8s;
}
@media only screen and (max-width: 768px) {
  .Header .HeaderSocial ul li a svg {
    fill: #604848;
  }
}
.Header .HeaderSocial ul li a:hover svg {
  transform: rotate(360deg);
  fill: #c0c0a8;
}
.Header__bar {
  padding: 10px 0;
  background-color: #604848;
}
.Header__bar_container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
}
.Header__bar_container .HeaderContact {
  display: flex;
  align-items: center;
  gap: 20px;
}
.Header__bar_container .HeaderContact a, .Header__bar_container .HeaderContact p {
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  transition: all ease 0.8s;
  display: flex;
  align-items: center;
  gap: 10px;
}
@media only screen and (max-width: 450px) {
  .Header__bar_container .HeaderContact a, .Header__bar_container .HeaderContact p {
    font-size: 3.5vw;
    line-height: 4vw;
  }
}
.Header__bar_container .HeaderContact a svg, .Header__bar_container .HeaderContact p svg {
  width: 22px;
  fill: #fff;
  transition: all ease 0.8s;
}
@media only screen and (max-width: 450px) {
  .Header__bar_container .HeaderContact a, .Header__bar_container .HeaderContact p {
    font-size: 12px;
    line-height: 14px;
  }
}
@media only screen and (max-width: 450px) and (max-width: 450px) {
  .Header__bar_container .HeaderContact a, .Header__bar_container .HeaderContact p {
    font-size: 3vw;
    line-height: 3.5vw;
  }
}
@media only screen and (max-width: 450px) {
  .Header__bar_container .HeaderContact a svg, .Header__bar_container .HeaderContact p svg {
    width: 20px;
  }
}
.Header__bar_container .HeaderContact a:hover {
  color: #c0c0a8;
}
.Header__bar_container .HeaderContact a:hover svg {
  fill: #c0c0a8;
}
.Header__content {
  background-color: #fff;
  padding: 20px 0;
}
.Header__content_container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.Header__content_container .HeaderLogo a {
  display: flex;
  align-items: center;
  gap: 10px;
}
.Header__content_container .HeaderLogo a p {
  font-size: 16px;
  line-height: 18px;
  font-family: "Heebo";
  font-weight: 600;
  color: #604848;
  letter-spacing: 1px;
}
@media only screen and (max-width: 450px) {
  .Header__content_container .HeaderLogo a p {
    font-size: 4vw;
    line-height: 4.5vw;
  }
}
.Header__content_container .HeaderMenu__button {
  width: 40px;
  height: 30px;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 1024px) {
  .Header__content_container .HeaderMenu__button {
    display: flex;
  }
}
.Header__content_container .HeaderMenu__button div {
  width: 100%;
  height: 5px;
  background-color: #604848;
  transition: all ease 0.8s;
}
.Header__content_container .HeaderMenu__button div:nth-of-type(1) {
  margin-bottom: 5px;
}
.Header__content_container .HeaderMenu__button div:nth-of-type(3) {
  margin-top: 5px;
}
@media only screen and (max-width: 1024px) {
  .Header__content_container .HeaderMenu nav {
    width: 100%;
    height: calc(100vh - 100%);
    background-color: #f0f0f0;
    transition: all ease 0.8s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
  }
}
@media only screen and (max-width: 768px) {
  .Header__content_container .HeaderMenu nav {
    padding-bottom: 60px;
  }
}
.Header__content_container .HeaderMenu nav .HeaderMenuList {
  display: flex;
  gap: 10px;
}
@media only screen and (max-width: 1024px) {
  .Header__content_container .HeaderMenu nav .HeaderMenuList {
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.Header__content_container .HeaderMenu nav .HeaderMenuList li.active-link-menu a {
  background-color: #604848;
  color: #f0f0f0;
}
.Header__content_container .HeaderMenu nav .HeaderMenuList li a {
  font-size: 16px;
  line-height: 18px;
  padding: 10px 20px;
  color: #604848;
  font-family: "Heebo";
  text-transform: uppercase;
  transition: all ease 0.8s;
  letter-spacing: 1px;
  border-radius: 999px;
}
@media only screen and (max-width: 450px) {
  .Header__content_container .HeaderMenu nav .HeaderMenuList li a {
    font-size: 4vw;
    line-height: 4.5vw;
  }
}
@media only screen and (max-width: 1024px) {
  .Header__content_container .HeaderMenu nav .HeaderMenuList li a {
    font-size: 20px;
    line-height: 22px;
    padding: 15px 40px;
  }
}
@media only screen and (max-width: 1024px) and (max-width: 450px) {
  .Header__content_container .HeaderMenu nav .HeaderMenuList li a {
    font-size: 5vw;
    line-height: 5.5vw;
  }
}
.Header__content_container .HeaderMenu nav .HeaderMenuList li a:hover {
  background-color: #604848;
  color: #f0f0f0;
}
.Header__content_container .HeaderMenu nav .HeaderSocial {
  display: none;
}
@media only screen and (max-width: 768px) {
  .Header__content_container .HeaderMenu nav .HeaderSocial {
    display: block;
  }
}
.Header--active .Header__bar_container .HeaderSocial {
  opacity: 1;
  pointer-events: all;
}
.Header--active .Header__content_container .HeaderMenu__button div:nth-of-type(1) {
  margin-top: 10px;
  margin-bottom: 0;
  transform: rotate(45deg);
}
.Header--active .Header__content_container .HeaderMenu__button div:nth-of-type(2) {
  opacity: 0;
}
.Header--active .Header__content_container .HeaderMenu__button div:nth-of-type(3) {
  margin-top: -10px;
  transform: rotate(-45deg);
}
.Header--active .Header__content_container .HeaderMenu nav {
  opacity: 1;
  pointer-events: all;
}

.Button {
  width: fit-content;
  padding: 20px 40px;
  background-color: #99733C;
  color: #fff;
  font-size: 16px;
  line-height: 18px;
  font-weight: 600;
  font-family: "Heebo";
  border: 1px solid transparent;
  transition: all ease 0.8s;
  display: flex;
  align-items: center;
  gap: 10px;
}
@media only screen and (max-width: 450px) {
  .Button {
    font-size: 4vw;
    line-height: 4.5vw;
  }
}
.Button:hover {
  background-color: #f0f0f0;
  color: #99733C;
}
.Button--secondary {
  background-color: #484848;
  color: #c0c0a8;
}
.Button--secondary:hover {
  background-color: #c0c0a8;
  color: #484848;
}
.Button--outline {
  border-color: #99733C;
  color: #99733C;
  background-color: rgba(0, 0, 0, 0.25);
}
.Button--outline:hover {
  border-color: #f0f0f0;
  background-color: #f0f0f0;
  color: #99733C;
}

.Project {
