.Values {
  padding: 60px 0;
  background-image: url("https://img.freepik.com/fotos-gratis/apertar-de-maos-de-empresarios_587448-5043.jpg?t=st=1713808073~exp=1713811673~hmac=290bd2969d78df3164a12346e41a6661a57e845a940156a28f4f87389c6ee794&w=1380");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: block;
  position: relative;
}
.Values::before {
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.45);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.Values__container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
}
.Values__container_title {
  margin-bottom: 40px;
  text-align: center;
}
.Values__container_title h2, .Values__container_title span {
  font-size: 44px;
  line-height: 46px;
  color: #fff;
  text-transform: uppercase;
  font-family: "Heebo";
  display: inline;
}
@media only screen and (max-width: 450px) {
  .Values__container_title h2, .Values__container_title span {
    font-size: 11vw;
    line-height: 11.5vw;
  }
}
.Values__container_title h2 span, .Values__container_title span span {
  color: #c0c0a8;
}
.Values__container .ValuesList {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}
@media only screen and (max-width: 1024px) {
  .Values__container .ValuesList {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (max-width: 450px) {
  .Values__container .ValuesList {
    grid-template-columns: 1fr;
  }
}
.Values__container .ValuesList__card {
  padding: 20px;
  border: 1px solid rgba(255, 255, 255, 0.45);
